import { Component, OnInit } from '@angular/core';
import { RestService } from '../provider/rest.service';

@Component({
  selector: 'app-clienthistory',
  templateUrl: './clienthistory.page.html',
  styleUrls: ['./clienthistory.page.scss'],
})
export class ClienthistoryPage implements OnInit {
  apiResponse: any;
  historyArr: any;

  constructor(private RestService: RestService) { }

  ngOnInit() {
    this.getCloserId();
  }

  getCloserId() {
    {
      var ticketData = JSON.parse(localStorage.ticket);
      var userData = JSON.parse(localStorage.userData);

      var Token = userData.Token;
      var custId = ticketData.CustomerId;
      this.RestService.requestViaGet('app/EngineerTickets/GetCustomerTicketHistory/' + custId + '/' + Token, '').then((response) => {
        console.log(response);
        this.apiResponse = response;
        if (this.apiResponse.Message == "success") {
          this.historyArr = this.apiResponse.result;
        } else {
        }
      });
    }
  }
}
